'use strict';

// MODULES //

var isnan = require( '@stdlib/math-base-assert-is-nan' );


// MAIN //

/**
* Returns the mode for a half-normal distribution with scale parameter `sigma`.
*
* @param {PositiveNumber} sigma - scale parameter
* @returns {number} mode
*
* @example
* var y = mode( 1.0 );
* // returns 0.0
*
* @example
* var y = mode( 2.0 );
* // returns 0.0
*
* @example
* var y = mode( NaN );
* // returns NaN
*
* @example
* var y = mode( 0.0 );
* // returns NaN
*/
function mode( sigma ) {
	if (
		isnan( sigma ) ||
		sigma <= 0.0
	) {
		return NaN;
	}
	return 0.0;
}


// EXPORTS //

module.exports = mode;
